import React from 'react'
import styled from 'styled-components'
import './styles.css'

const TarefaList = styled.ul`
  padding: 0;
  width: 200px;
`

const Tarefa = styled.li`
  text-align: left;
  text-decoration: ${({completa}) => (completa ? 'line-through' : 'none')};
`

const InputsContainer = styled.div`
  display: grid;
  grid-auto-flow: column;
  gap: 10px;
`

class App extends React.Component {
    state = {
      tarefas: [],
      inputValue: '',
      filter: ''
    }

  componentDidUpdate() {
    localStorage.setItem('tarefas', JSON.stringify(this.state.tarefas));
  };

  componentDidMount() {
    this.setState({tarefas: JSON.parse(localStorage.getItem('tarefas'))});
  };

  onChangeInput = (event) => {
    this.setState({ inputValue: event.target.value });
  }

  criaTarefa = () => {
    const novaTarefa = {
      id: Date.now(),
      texto: this.state.inputValue,
      completa: false
    }

    const novasTarefas = this.state.tarefas;
    novasTarefas.push(novaTarefa);

    this.setState({ tarefa: novasTarefas, inputValue:'' })
  }

  selectTarefa = (id) => {
    const novasTarefas = this.state.tarefas;
    novasTarefas.forEach(tarefa => {
      if (tarefa.id === id) {
        tarefa.completa = !tarefa.completa
      }
    });

    this.setState({ tarefa: novasTarefas });
  }

  apagaTarefa = (id) => {
    const novasTarefas = this.state.tarefas;
    let idxApagado;
    novasTarefas.forEach((tarefa, idx) => {
      if (tarefa.id === id) {
        idxApagado = idx;
      }
    });
    novasTarefas.splice(idxApagado, 1);

    this.setState({ tarefa: novasTarefas });
  }

  onChangeFilter = (event) => {
    this.setState({filter: event.target.value})
  }

  apagaTudo = () => {
    const novasTarefas = this.state.tarefas;
    novasTarefas.splice(0, novasTarefas.length)
    this.setState({ tarefa: novasTarefas });
  }

  render() {
    const listaFiltrada = this.state.tarefas
      .filter(tarefa => {
        switch (this.state.filter) {
          case 'pendentes':
            return !tarefa.completa
          case 'completas':
            return tarefa.completa
          default:
            return true
        }
      })

    return (
      <div className="App">
        <h1>Lista de tarefas</h1>
        <InputsContainer>
          <input value={this.state.inputValue} onChange={this.onChangeInput}/>
          <button onClick={this.criaTarefa}>Adicionar</button>
        </InputsContainer>
        <br/>

        <InputsContainer>
          <label>Filtro</label>
          <select value={this.state.filter} onChange={this.onChangeFilter}>
            <option value="">Nenhum</option>
            <option value="pendentes">Pendentes</option>
            <option value="completas">Completas</option>
          </select>
        </InputsContainer>
        <TarefaList>
          {listaFiltrada.map((tarefa, idx) => {
            return (
              <Tarefa
                key={idx}
                completa={tarefa.completa}
                onClick={() => this.selectTarefa(tarefa.id)}
                onDoubleClick={() => this.apagaTarefa(tarefa.id)}
              >
                {tarefa.texto}
              </Tarefa>
            )
          })}
        </TarefaList>
        <button onClick={this.apagaTudo}>Apagar Tudo</button>
      </div>
    )
  }
}

export default App
